import React from 'react';
import PropTypes from 'prop-types';
import Button from '@material-ui/core/Button';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogContentText from '@material-ui/core/DialogContentText';
import DialogTitle from '@material-ui/core/DialogTitle';

class ResponsiveDialog extends React.PureComponent {
    render() {
        const { fullScreen, open = false, title, content, positiveText = "Yes", negativeText, negativeHandler = e => null, positiveHandler } = this.props;

        return (
            <Dialog
                fullScreen={fullScreen}
                open={open}
                onClose={negativeHandler}
                aria-labelledby={title}
            >
                <DialogTitle id={title}>{title}</DialogTitle>
                <DialogContent>
                    {
                        typeof (content) === "string" ?
                            <DialogContentText>
                                {content}
                            </DialogContentText>
                            : content
                    }
                </DialogContent>
                <DialogActions>
                    {
                        negativeText ?
                            <Button onClick={negativeHandler} color="primary" autoFocus>
                                {negativeText}
                            </Button> : null
                    }
                    {
                        positiveHandler ?
                            <Button onClick={positiveHandler} color="primary">
                                {positiveText}
                            </Button> : null
                    }
                </DialogActions>
            </Dialog>
        );
    }
}

ResponsiveDialog.propTypes = {
    fullScreen: PropTypes.bool.isRequired,
};

export default ResponsiveDialog;


